/*******************************************************
Animation
*******************************************************/
@keyframes popIn {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(1.2, 1);
  }
  50% {
    transform: scale(1.4, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes popOut {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(1.2, 1);
  }
  50% {
    transform: scale(1.4, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes splashIn {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  25% {
    transform: scale(1.1);
    opacity: 0.8;
  }
  50% {
    transform: scale(1.1);
    opacity: .9;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes splashOut {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  25% {
    transform: scale(1);
    opacity: 0.8;
  }
  50% {
    transform: scale(1);
    opacity: .9;
  }
  100% {
    transform: scale(0.5);
    opacity: 1;
  }
}
